import app from "../../App.vue"


	// ,
	var _self, page = 1,timer = null;

	export default {
		data() {
			return {
				list:[],
				totalCalorie:'',
				totalTime:'',
				hour:'',
				minute:'',
				loadingText: '',
				page:0,//
				//apiUrl:'https://www.ymznkf.com/wx_server',//
				id:'',//,locakStorage 
				ids:[],
				del_id:''//locakStorage  
			}
		},
		components:{

		},
		mounted(){
			var a = document.getElementsByClassName('uni-page-head-hd')[0]
			a.style.display = 'none';
		},
		onLoad(options) {
			if(this.list=null&&options.list==null) return
			else if(options.list!=null){
				const item = JSON.parse(options.list);
				this.list=item
				//console.log(item)
				this.totalCalorie=options.totalCalorie
				this.totalTime=options.totalTime
				this.hour=parseInt(this.totalTime)
				this.minute=60*(this.totalTime-parseInt(this.totalTime))
				this.ids=[]
				for(var i=0;i<item.length;i++){
					//this.id=item[i].options[0].id
					this.ids.push(item[i].options[0].id)
				}
			}
			
			_self = this;
			
			//,
			//if(this.checkLogin()==false){
			//	return;
			//}
			
			//this.getLaction();//gps

			this.page=0;

			//id
			if (options.id != null && options.id !=""){
				this.id=options.id;
			}   
			//,
			//this.Refresh("init");

		},
		onShow() {
			//if(this.checkLogin()==false){
			//	return;
			//}

			//,
			//this.Refresh("init");
		},
		onPullDownRefresh: function() {
			//
			this.Refresh();
		},
		methods: {
			back(){
				uni.navigateBack()
			},
			/**
			* waiting_for_restaurant_order_3_3
			*   data-index="{{index}}" 
			*   var index = e.currentTarget.dataset.index; var cata = this.list_cata_list[index];
			*/
			waiting_for_restaurant_order_3_3_click:function(event){
			
			},
			
			/**
			* waiting_for_restaurant_order_6_6
			*   data-index="{{index}}" 
			*   var index = e.currentTarget.dataset.index; var cata = this.list_cata_list[index];
			*/
			waiting_for_restaurant_order_6_6_click:async function(event){
				//console.log(this.list)
				const params={
					planIds:this.ids,
					totalCalorie:this.totalCalorie,
					totalTime:this.totalTime
				}
				await this.$u.api.generateOrder(params)
				await this.$u.api.deletes({planIds:this.ids})
				uni.switchTab({
					url: '../center/index'
				});
			},
			
			

			//
			Refresh:function(_action) {
			
				uni.showLoading();
				
				//
				var that = this
				var url=that.apiUrl+'{server_code_file_path}';
				console.log(url);
				uni.request({
				url: url,//
				data: {
					action: _action,//click
					uid: that.getUid(),//id,
					//
					//---start
					
					//---end
					id: that.id,//idid
					del_id: that.del_id,//
					page:that.page
				},
				method: 'GET',
				success: function (res) {//

					//   
					uni.hideNavigationBarLoading();
					//   
					uni.stopPullDownRefresh();  

					//   
					uni.hideLoading();  

					var tmp = res.data;

					//
					if(_action=="init"){

					}

{deal_listpages}



					//
					if (tmp.message != null && tmp.message != "") {
						uni.showToast({
						title: tmp.message,
						icon: 'none',
						duration: 2000
						})
					}



					//
					if(tmp.page!=null && tmp.page!=""){
						page=tmp.page;
					}					

				},
				fail: function (res) {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 2000
					})
					console.log(res.data);
					console.log('is failed')
				}
				})
			},
		}
	}
