import app from "../../App.vue"


	// ,
	var _self, page = 1,timer = null;

	export default {
		data() {
			return {
				list:[],
				loadingText: '',
				animation: '',
				animationData: {},
					list_7: [
					{
				
					},
					{
				
					},
					{
				
					},
					],
				
			
 
				page:0,//
				apiUrl:'https://www.ymznkf.com/wx_server',//
				id:'',//,locakStorage  
				del_id:''//locakStorage  
			}
		},
		components:{

		},
		onLoad(options) {
			this.list=JSON.parse(options.list)
			console.log(this.list)
			_self = this;
			
			//,
			//if(this.checkLogin()==false){
			//	return;
			//}
			
			//this.getLaction();//gps

			this.page=0;

			//id
			if (options.id != null && options.id !=""){
				this.id=options.id;
			}  
			
			// 
			var animation = uni.createAnimation({
				transformOrigin: "50% 0 50%",
				duration: 1000, //1
				timingFunction: 'linear', //linear 
				delay: 200 //
			})
			this.animation = animation;
			this.scaleAndScale();

			//,
			//this.Refresh("init");

		},
		onShow() {
			console.log("on show");
			//if(this.checkLogin()==false){
			//	return;
			//}

			//,
			//this.Refresh("init");
		},
		onPullDownRefresh: function() {
			//
			this.Refresh();
		},
		methods: {
			// 
			scaleAndScale() {
				// 
				this.animation.opacity(0, 1).step() //1,2
				this.animation.opacity(1, 0).step() //
				// data
				this.animationData = this.animation.export(); // 
			},
			noscaleAndScale() {
				this.animation.scale(1, 1).step()
				this.animationData = this.animation.export()
			},
			/**
			* prescription_label_30_30
			*   data-index="{{index}}" 
			*   var index = e.currentTarget.dataset.index; var cata = this.list_cata_list[index];
			*/
			prescription_label_30_30_click:function(event){
			
			},
			
			

			//
			Refresh:function(_action) {
			
				uni.showLoading();
				
				//
				var that = this
				var url=that.apiUrl+'{server_code_file_path}';
				console.log(url);
				uni.request({
				url: url,//
				data: {
					action: _action,//click
					uid: that.getUid(),//id,
					//
					//---start
					
					//---end
					id: that.id,//idid
					del_id: that.del_id,//
					page:that.page
				},
				method: 'GET',
				success: function (res) {//

					//   
					uni.hideNavigationBarLoading();
					//   
					uni.stopPullDownRefresh();  

					//   
					uni.hideLoading();  

					var tmp = res.data;

					//
					if(_action=="init"){

					}





					//
					if (tmp.message != null && tmp.message != "") {
						uni.showToast({
						title: tmp.message,
						icon: 'none',
						duration: 2000
						})
					}



					//
					if(tmp.page!=null && tmp.page!=""){
						page=tmp.page;
					}					

				},
				fail: function (res) {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 2000
					})
					console.log(res.data);
					console.log('is failed')
				}
				})
			},
		}
	}
