// vmvuethisvuextoken
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
	// baseUrl: 'http://localhost:8082', // 
	baseUrl: '', // 
	method: 'POST',
	// jsonJSON.parse()
	dataType: 'json',
	showLoading: true, // loading
	loadingText: '...', // loading
	loadingTime: 800, // ms
	originalData: false, // 
	loadingMask: true, // loading
	// 
	header: {
		'content-type':  'application/json;charset=UTF-8'
	},
});
	
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		
		config.header.Authorization = "Bearer " + vm.vuex_token
		
		
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorage
		// Storage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;
		//config.header.Token = 'xxxxxx';
		
		// urlurlthis.$u.get(url)url
		if(config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}
	
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		//
		const {code,data}=res

		if(code == 200) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return data;
		}
		else if(code == 400){
			vm.$u.toast(res.message);
			return false
		}
		else if(code == 401){
			vm.$u.toast("");
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/auth/login')
			}, 1500)
			return false;
		}
		else if(code == 404) {
			vm.$u.toast(res.message);
			setTimeout(() => {
				// uView
				vm.$u.route('/pages/auth/login')
			}, 1500)
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}